.contactform {
  padding-top: 50px;
  /* color: blue; */
}

.contactform label {
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
  font-size: 18px;
  font-weight: 500;
}

.contactform button {
  display: flex;
  margin-right: auto;
  margin-left: auto;
  width: 400px;
  text-align: center;
  font-weight: 700;
}
/* .contactform button:hover {
  background: #1872bd;
} */

.contactform_title {
  font-size: 32px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 50px;
}

.contactform input {
  width: 400px;
  height: 40px;
  border: 1px solid rgba(33, 33, 33, 0.2);
  box-sizing: border-box;
  border-radius: 4px;
  padding-left: 10px;
  outline: none;
  cursor: pointer;
  margin-bottom: 30px;
  transition: border-color 400ms cubic-bezier(0.4, 0, 0.2, 1);
}

.contactform input:hover {
  border-color: #2196f3;
}
